import './AdminOrdenVentaDetail.css'

import {
  IonContent,
  IonDatetime,
  IonHeader,
  IonItem,
  IonLabel,
  IonList,
  IonModal,
  IonRadio,
  IonRadioGroup,
  IonTitle,
  IonToolbar,
} from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useEffect, useState } from 'react'
import { Button } from 'react-bootstrap'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'

import { AdminItemDetail, CalificacionTransportista, Page, SMap } from '../../components'
import ChatModal from '../../components/Chat/ChatModal'
import LinkButton from '../../components/LinkButton'
import LoaderAdmin from '../../components/LoaderAdmin'
import { PEDIDO_ESTADOS, RUTAS } from '../../constants'
import { fecha_fmt, plata_fmt } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'
import { getEstadoOrdenVentaDisplay } from '../../models/types/Pedido'
import { OrdenVenta, PuntoRetiro } from '../../services/api'
import {DetailViewProps, formatTextoURL} from '../../helpers-ts'

const moment = require('moment-timezone')

const QRCode = require('qrcode.react')

export const AdminOrdenVentaDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [OrdenVenta, setOrdenVenta] = useState<OrdenVenta>()
  const [showModal, setShowModal] = useState(false)
  const [selected, setSelected] = useState<number | null>(null)
  const [Lat, setLat] = useState(-25.30066)
  const [Lng, setLng] = useState(-57.63591)
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 })
  const [Ready, setReady] = useState(false)
  const [selectedDate, setSelectedDate] = useState('')
  const [minDate, setMinDate] = useState('')
  const [PuntosRetiro, setPuntosRetiro] = useState<PuntoRetiro[]>([])
  const [CalificacionModalVisible, setCalificacionModalVisible] = useState(false)
  const [CalificacionHabilitada, setCalificacionHabilitada] = useState(false)
  const [Loading, setLoading] = useState(true)
  const [chatVisible, setChatVisible] = useState(false)
  const idOrden = match.params.id

  const setup = () => {
    console.log(match.params.id)
    obtenerOrden(parseInt(idOrden))
    setReady(true)
  }

  useEffect(() => {
    setup()
  }, [idOrden])

  const obtenerOrden = (idOrden: number) => {
    rootStore.environment.api
      .adminGetOrdenVenta(idOrden)
      .then((result) => {
        if (result.kind === 'ok') {
          // @ts-ignore
          setOrdenVenta(result.orden_venta)
          setPuntosRetiro(result.orden_venta.fabricante.puntos_retiro)
          if (result.orden_venta.fabricante.puntos_retiro.length == 1) {
            setSelected(result.orden_venta.fabricante.puntos_retiro[0].id)
          } else {
            setSelected(null)
          }
          setSelectedDate(result.orden_venta.pedido.fecha_envio)
          setMinDate(result.orden_venta.pedido.fecha_envio)
          if (result.orden_venta.punto_retiro) {
            const ubicacion = result.orden_venta.punto_retiro.ubicacion.split(',')
            setLat(parseFloat(ubicacion[0]))
            setLng(parseFloat(ubicacion[1]))
            setCenter({ lat: parseFloat(ubicacion[0]), lng: parseFloat(ubicacion[1]) })
          }
          if (result.orden_venta.estado === 20) {
            const orden_venta = result.orden_venta
            const itinerarioCalificar = {
              tipo_calificacion: 30,
              id_pedido: orden_venta.pedido.id,
              nombre: orden_venta.transportista.usuario.first_name + ' ' + orden_venta.transportista.usuario.last_name,
              imagen: orden_venta.transportista.usuario.imagen_perfil,
              id_calificar: orden_venta.transportista.id,
            }
            rootStore.setPedidoCalificar(itinerarioCalificar)
            verificarCalificacion(
              itinerarioCalificar.id_pedido,
              itinerarioCalificar.tipo_calificacion,
              itinerarioCalificar.id_calificar,
            )
            setLoading(false)
          } else {
            setLoading(false)
          }
        }
      })
      .catch((err) => {
        setLoading(false)
      })
  }

  const verificarCalificacion = (id_pedido: number, tipo_calificacion: number, id_calificar: number) => {
    rootStore.environment.api.verificar_calificacion(id_pedido, tipo_calificacion, id_calificar).then((result) => {
      if (result.kind === 'ok' && result.data) {
        setCalificacionHabilitada(true)
      }
    })
    setLoading(false)
  }

  const aprobarOrdenVenta = () => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Confirma que desea aprobar el pedido?')) {
      return false
    }
    setShowModal(false)
    setLoading(true)
    // @ts-ignore
    rootStore.environment.api.aprobarPedido(OrdenVenta.id, selected, selectedDate).then((result) => {
      setLoading(false)
      if (result.kind === 'ok') {
        obtenerOrden(parseInt(history.location.pathname.split('/')[4]))
      } else if (result.kind === 'bad-data') {
        if (result.errors.non_field_errors) {
          alert(result.errors.non_field_errors[0])
        }
      } else {
        alert('Ocurrió un error al guardar vuelva a intentar en unos momentos')
      }
    })
  }

  const imprimirCodigos = () => {
    const idOrden = history.location.pathname.split('/')[4]
    const win = window.open('/admin/imprimir/' + idOrden, '_blank')
    // @ts-ignore
    win.focus()
  }

  return (
    <Page login_required admin>
      <IonModal isOpen={showModal} backdropDismiss={false}>
        <IonHeader>
          <IonToolbar>
            <IonTitle>Confirmar Orden de Venta</IonTitle>
          </IonToolbar>
        </IonHeader>
        <IonContent>
          <IonItem>
            <IonLabel>
              <strong>Fecha de retiro</strong>
            </IonLabel>
            <IonDatetime displayFormat="DD/M/YYYY" readonly={true} value={selectedDate} placeholder={'-'} />
          </IonItem>
          <IonItem>
            <IonLabel>
              <strong>Hora de retiro</strong>
            </IonLabel>
            <IonDatetime
              displayFormat="H:mm"
              value={selectedDate}
              min={moment(minDate).format()}
              cancelText={'Cancelar'}
              doneText={'Seleccionar'}
              minuteValues="0,15,30,45"
              placeholder={'-'}
              onIonChange={(e) => {
                if (e && e.target) {
                  const target = e.target as HTMLInputElement
                  setSelectedDate(moment(target.value).format())
                }
              }}
            />
          </IonItem>
          <IonList>
            <IonItem>
              <IonLabel>
                <strong>Punto de retiro</strong>
              </IonLabel>
            </IonItem>
            <IonRadioGroup value={selected} onIonChange={(e) => setSelected(e.detail.value)}>
              {PuntosRetiro &&
                PuntosRetiro.map((item: any) => {
                  return (
                    <IonItem key={'id-' + item.id}>
                      <IonLabel>
                        {item.nombre} - {item.calle_principal} {item.numero}
                      </IonLabel>
                      <IonRadio slot="end" value={item.id} />
                    </IonItem>
                  )
                })}
            </IonRadioGroup>
          </IonList>
        </IonContent>
        <div style={{ padding: 10 }}>
          <div className={'row'}>
            <div className={'col-12 col-md-6 mb-xs-2'}>
              <button
                className={'btn btn-secondary btn-block'}
                onClick={() => {
                  setSelected(null)
                  setSelectedDate(minDate)
                  setShowModal(!showModal)
                }}
              >
                Cancelar
              </button>
            </div>
            {minDate && (
              <div className={'col-12 col-md-6'}>
                <button
                  className={'btn btn-primary btn-block'}
                  disabled={!selected || selectedDate === ''}
                  onClick={() => {
                    aprobarOrdenVenta()
                  }}
                >
                  Aprobar
                </button>
              </div>
            )}
          </div>
        </div>
      </IonModal>
      <CalificacionTransportista
        color={'var(--azul-fabricante)'}
        visible={CalificacionModalVisible}
        onAceptar={() => {
          setCalificacionModalVisible(false)
          setCalificacionHabilitada(false)
        }}
        onCerrar={() => {
          setCalificacionModalVisible(false)
        }}
      />
      <LoaderAdmin visible={Loading} />
      {!Loading && OrdenVenta && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">Orden de venta {OrdenVenta.referencia}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a href="/admin/orden_venta">Ordenes de venta</a>
                  </li>
                  <li className="breadcrumb-item active">{OrdenVenta.referencia}</li>
                </ol>
              </div>
            </div>
            <div className={'text-right'}>
              <div className={'btn-group ml-auto'}>
                {OrdenVenta.estado === 0 && (
                  <div className={'mb-2 text-right'}>
                    <button
                      className={'btn btn-light ml-3'}
                      title={'Confirmar orden de venta'}
                      onClick={() => setShowModal(true)}
                    >
                      <FIcon.Check size={14} /> Confirmar
                    </button>
                  </div>
                )}
                {OrdenVenta.estado === 20 && CalificacionHabilitada && (
                  <div className={'mb-2 text-right'}>
                    <button className={'btn btn-light ml-3'} onClick={() => setCalificacionModalVisible(true)}>
                      <FIcon.Star size={14} style={{ marginBottom: 3 }} /> Calificar driver
                    </button>
                  </div>
                )}
                {OrdenVenta.estado !== 0 && (
                  <div className={'mb-2 text-right'}>
                    <button className={'btn btn-light ml-3'} onClick={() => imprimirCodigos()}>
                      <FIcon.Printer size={14} /> Imprimir
                    </button>
                  </div>
                )}
              </div>
            </div>
            <div className="card">
              <div className="card-body">
                <div className={'row mx-1 mt-3'}>
                  {loginStore.administrador && (
                    <AdminItemDetail titulo={'Proveedor'} texto={OrdenVenta.fabricante.nombre} columnas={3} />
                  )}
                  <AdminItemDetail
                    titulo={'Precio Mejorado'}
                    texto={OrdenVenta.precio_mejorado ? 'Si' : 'No'}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Estado'}
                    texto={getEstadoOrdenVentaDisplay(OrdenVenta.estado)}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Fecha de compra'}
                    texto={fecha_fmt(OrdenVenta.fecha, 'DD/MM/YYYY HH:mm')}
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Fecha confirmación'}
                    texto={
                      OrdenVenta.fecha_confirmado
                        ? fecha_fmt(OrdenVenta.fecha_confirmado, 'DD/MM/YYYY HH:mm')
                        : 'PENDIENTE'
                    }
                    columnas={3}
                  />
                  <AdminItemDetail
                    titulo={'Fecha de retiro'}
                    texto={
                      OrdenVenta.fecha_retiro ? fecha_fmt(OrdenVenta.fecha_retiro, 'DD/MM/YYYY HH:mm') : 'PENDIENTE'
                    }
                    columnas={3}
                  />
                  {OrdenVenta.tipo_vehiculo && (
                    <AdminItemDetail titulo={'Tipo de vehículo'} texto={OrdenVenta.tipo_vehiculo.nombre} columnas={3} />
                  )}
                  <AdminItemDetail
                    titulo={'Costo total'}
                    separador_dinero
                    texto={OrdenVenta.costo_total}
                    columnas={12}
                  />
                </div>
              </div>
            </div>
            <h4>Detalle</h4>
            <div className="card">
              <div className="card-body">
                <div className={'row'}>
                  <div className={'col-10 card'}>
                    <div className={'table-responsive'}>
                      <table className={'table'}>
                        <thead>
                          <tr>
                            <th colSpan={3}>
                              <LinkButton href={RUTAS.ADMIN_PEDIDO_DETAIL(OrdenVenta.pedido.id)}>
                                <strong>Pedido #{OrdenVenta.pedido.referencia}</strong>
                              </LinkButton>
                            </th>
                          </tr>
                          <tr>
                            <th>Producto</th>
                            <th>Cantidad</th>
                            <th>Costo</th>
                          </tr>
                        </thead>
                        <tbody>
                          {OrdenVenta.pedido.productos.map((prod, index) => {
                            return (
                              <tr key={index}>
                                <td>
                                  <LinkButton href={RUTAS.PRODUCTO_DETAIL(prod.id, formatTextoURL(prod.nombre))}>
                                    {prod.nombre}
                                  </LinkButton>
                                </td>
                                <td>{prod.cantidad}</td>
                                <td>{plata_fmt(prod.costo)}</td>
                              </tr>
                            )
                          })}
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div className={'col-2'}>
                    {OrdenVenta.contrasena && (
                      <div className={'text-center'}>
                        <QRCode value={OrdenVenta.contrasena} size={128} className={'mx-auto pt-2'} />
                        <h1 className={'pt-2'} style={{ color: 'black' }}>
                          <strong>{OrdenVenta.contrasena}</strong>
                        </h1>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
            {OrdenVenta.transportista && (
              <h4 className={'mx-3 mt-3'}>
                <strong>Transportista</strong>
              </h4>
            )}
            {OrdenVenta.transportista && (
              <div className={'col-12 mt-3'}>
                <div className={'row'}>
                  <AdminItemDetail
                    titulo={'Nombre'}
                    texto={
                      OrdenVenta.transportista.usuario.first_name + ' ' + OrdenVenta.transportista.usuario.last_name
                    }
                    columnas={6}
                  />
                  {OrdenVenta.vehiculo && (
                    <AdminItemDetail
                      titulo={'Vehículo'}
                      texto={
                        OrdenVenta.vehiculo.marca + ' ' + OrdenVenta.vehiculo.modelo + ' ' + OrdenVenta.vehiculo.ano
                      }
                      columnas={6}
                    />
                  )}
                  {OrdenVenta.vehiculo && (
                    <AdminItemDetail titulo={'Color'} texto={OrdenVenta.vehiculo.color} columnas={6} />
                  )}
                  {OrdenVenta.vehiculo && (
                    <AdminItemDetail titulo={'Chapa'} texto={OrdenVenta.vehiculo.numero_chapa} columnas={6} />
                  )}

                  <Button
                    className={'btn btn-light ml-3 mb-3'}
                    onClick={() => {
                      setChatVisible(true)
                    }}
                  >
                    Contactar Driver
                  </Button>
                </div>
              </div>
            )}
            {OrdenVenta.punto_retiro && (
              <h4 className={'mx-3'}>
                <strong>Punto de retiro seleccionado</strong>
              </h4>
            )}
            {OrdenVenta.punto_retiro && (
              <div className="card">
                <div className="card-body">
                  <div className={'col-12 mt-3'}>
                    <div className={'row'}>
                      <AdminItemDetail titulo={'Nombre'} texto={OrdenVenta.punto_retiro.nombre} columnas={3} />
                      <AdminItemDetail
                        titulo={'Calle principal'}
                        texto={OrdenVenta.punto_retiro.calle_principal}
                        columnas={3}
                      />
                      <AdminItemDetail
                        titulo={'Calle secundaria'}
                        texto={OrdenVenta.punto_retiro.calle_secundaria}
                        columnas={3}
                      />
                      <AdminItemDetail titulo={'Número'} texto={OrdenVenta.punto_retiro.numero} columnas={3} />
                      <AdminItemDetail titulo={'Referencia'} texto={OrdenVenta.punto_retiro.referencia} columnas={6} />
                    </div>
                    <SMap lat={Lat} lng={Lng} center={Center} ubicacion={null} bloquear_cambio_ubicacion />
                  </div>
                </div>
              </div>
            )}
          </div>
          {OrdenVenta.pedido.estado > PEDIDO_ESTADOS.pagado && (
            <ChatModal visible={chatVisible} pedido={OrdenVenta.pedido} setVisible={setChatVisible} />
          )}
        </div>
      )}
    </Page>
  )
})

export default AdminOrdenVentaDetail
